import { observer } from 'mobx-react';
import * as React from 'react';
import { Page, VPage, start } from 'tonva';
import { CustomHeaderTemplate } from 'tools/CustomHeaderTemplate';
import { CHome } from './CHome';

export class VScanCode extends VPage<CHome> {

    scan: any;
    async open(param?: any) {
        this.openPage(this.page);
    }

    media = () => {
        if (navigator.userAgent.indexOf("Html5Plus") > -1) {
            // @ts-ignore 
            // window.plusBarcode();
            this.scan = new plus.barcode.Barcode('bcid');
            this.scan.onmarked = this.onmarked;
            this.scan.start();
        }
    }

    onmarked = (type: any, result: any) => {
        var text = '未知: ';
        switch (type) {
            case 0:
                text = 'QR: ';
                break;
            case 1:
                text = 'EAN13: ';
                break;
            case 2:
                text = 'EAN8: ';
                break;
        }
        alert(text + result);
    }

    close = () => {
        if (navigator.userAgent.indexOf("Html5Plus") > -1) {
            // @ts-ignore 
            this.scan.close();
            // window.plusBccancel();
        }
        console.log('close');
        this.closePage();
    }

    private page = observer(() => {
        this.media();
        /* setTimeout(() => {
            this.media();
        }, 50); */
        let header = <div className='w-100 text-center text-light'>扫一扫</div>;
        return <div className="w-100 h-100 d-flex flex-column">
            <div className="w-100">
                {CustomHeaderTemplate(header, '', '#555555', '', this.close)}
            </div>
            {/* {<button onClick={() => this.close()}>关闭</button>} */}
            <main className="flex-fill">
                <div id='bcid' className="w-100 h-100" style={{ background: '#CCCCCC' }}></div>
            </main>
        </div>;
        /* return <Page header={header} right={<></>} className="h-100">
            <div id='bcid' className="w-100 h-100" style={{ background: '#CCCCCC' }}></div>
        </Page >; */
    });
}